<template>
  <v-navigation-drawer fixed app left mini-variant dark class="pink"
    style="display: flex; align-items: center; justify-content: center;">
    <v-list class="pa-0" style="margin-bottom: 40px;">
      <v-list-tile avatar v-for="(menu, index) in menus" :key="index" class="side-list-tile" 
        style="justify-content: center; margin-bottom: 48px;"
        :to="menu.url">
        <v-btn fab flat>
          <v-icon size=48>{{ menu.icon }}</v-icon>
        </v-btn>
      </v-list-tile>
    </v-list>
  </v-navigation-drawer>
</template>

<script>
export default {
  data () {
    return {
      menus: [
        {
          url: '/',
          icon: 'history'
        },
        {
          url: '/list',
          icon: 'dashboard'
        },
        {
          url: '/rank',
          icon: 'public'
        }
      ]
    }
  }
}
</script>

<style>
.side-list-tile > div {
  justify-content: center;
}
</style>



