import Vue from 'vue'
import Router from 'vue-router'
// import HelloWorld from '@/components/HelloWorld'
import Home from '@/components/Home'
import Rank from '@/components/Rank'
import ListLahan from '@/components/ListLahan'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/list',
      name: 'ListLahan',
      component: ListLahan
    },
    {
      path: '/rank',
      name: 'Rank',
      component: Rank
    }
  ]
})



