<template>
  <div>
    <my-toolbar/>
    <home-par/>
    <home-sm-menu v-if="$vuetify.breakpoint.smAndDown"/>
    <v-container>
      <v-layout row>
        <v-flex>
          <p>Pertanian merupakan pembudidayaan tanaman pada suatu lahan untuk menghasilkan sumber pangan. 
            Salah satu sumber pangan yang banyak dihasilkan adalah jagung. 
            Jagung merupakan tanaman semusim (annual). Satu siklus hidupnya diselesaikan dalam 80-150 hari. 
            Paruh pertama dari siklus merupakan tahap pertumbuhan vegetatif dan paruh kedua untuk 
            tahap pertumbuhan generatif. Tinggi tanaman jagung sangat bervariasi. 
            Meskipun tanaman jagung umumnya berketinggian antara 1m-3m, 
            ada varietas yang dapat mencapai tinggi 6m. 
            Tinggi tanaman biasa diukur dari permukaan tanah hingga ruas teratas 
            sebelum bunga jantan. Penanaman jagung disuatu lahan harus sesuai dengan persyaratan
             penggunaan lahan. Kesesuaian lahan tanaman jagung pada tingkat ordo
              dapat dibedakan menjadi 2 yaitu, sesuai, dan tidak sesuai.</p>
        </v-flex>
      </v-layout>
    </v-container>
  </div>
</template>

<script>
import MyToolbar from './MyToolbar'
import HomePar from './HomePar'
import HomeSmMenu from './HomeSmMenu'

export default {
  name: 'Home',
  components: {
    HomePar,
    HomeSmMenu,
    MyToolbar
  }
}
</script>

<style>

</style>



