<template>
  <div>
    <side-nav/>
    <v-container class="pa-0 ma-0" fill-height>
      <v-layout row>
        <v-flex md6>
          <div>
            <v-list two-line style="padding-bottom: 60px;">
              <transition-group name="flip-list" tag="div">
                <v-list-tile
                  avatar
                  v-for="(item, index) in items"
                  :key="index"
                  @click="sahyHi()"
                >
                  <v-list-tile-avatar>
                    <img :src="item.avatar">
                  </v-list-tile-avatar>

                  <v-list-tile-content>
                    <v-list-tile-title v-html="item.lahan.lokasi"></v-list-tile-title>
                    <v-list-tile-sub-title>
                      {{ item.lahan.lat }}, {{ item.lahan.long }}
                    </v-list-tile-sub-title>
                  </v-list-tile-content>

                  <v-list-tile-action>
                    <span v-if="item.pref == '--'">{{ item.pref }}</span>
                    <span v-else>{{ item.pref.toFixed(4) }}</span>
                  </v-list-tile-action>
                </v-list-tile>
              </transition-group>
            </v-list>
          </div>
        </v-flex>
        <v-flex col md6 justify-center align-center fill-height>
          <div id="rank-form-cont" class="pink darken-1">
            <h3 class="headline white--text"><strong>{{ totalItems }} data</strong></h3>
            <h3 class="display-1 white--text"><strong>Jalankan Perangkingan</strong></h3>
            <v-btn dark outline @click="runRank">Go!</v-btn>
          </div>
        </v-flex>
      </v-layout>
    </v-container>
    <full-loading v-if="isLoading"/>
  </div>
</template>

<script>
import FullLoading from './FullLoading'
import MyToolbar from './MyToolbar'
import SideNav from './SideNav'
import { getData, comp } from '../services/lahan'

const loadInterval = 1500

export default {
  components: {
    FullLoading,
    MyToolbar,
    SideNav
  },
  data () {
    return {
      items: [],
      isLoading: false
    }
  },
  created () {
    this.getAndTransform()
  },
  computed: {
    totalItems: function () {
      return this.items.length
    }
  },

  methods: {
    runRank () {
      this.isLoading = true
      comp()
        .catch(err => {
          console.log(err)
        })
        .then(items => {
          setTimeout(() => {
            this.isLoading = false
            this.items = items
          }, loadInterval)

          setTimeout(() => {
            this.items.sort((a, b) => {
              return b.pref - a.pref
            })
          }, loadInterval + 300)
        })
    },
    getAndTransform () {
      getData()
        .then(items => {
          this.items = items.map(lahan => {
            return {
              lahan,
              pref: '--'
            }
          })
        })
        .catch(err => {
          console.log(err)
        })
    }
  }
}
</script>

<style>
#rank-form-cont {
  min-height: 100vh;
  min-width: 40vw;
  position: fixed;
  right: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.flip-list-move {
  transition: transform 1s;
}
</style>



