<template>
  <v-toolbar
    app
    flat
    dark
    color="pink"
  >
    <v-toolbar-title v-text="title"></v-toolbar-title>
    <v-spacer></v-spacer>
    <v-toolbar-items v-if="$vuetify.breakpoint.mdAndUp">
      <v-btn flat v-for="menu in menus" :key="menu.title" :to="menu.path">{{ menu.title }}</v-btn>
    </v-toolbar-items>
  </v-toolbar>
</template>

<script>
export default {
  data () {
    return {
      menus: [
        {
          title: 'home',
          path: '/'
        },
        {
          title: 'list',
          path: '/list'
        },
        {
          title: 'Rangking',
          path: '/rank'
        }
      ],
      title: 'CornApp'
    }
  }
}
</script>

<style>

</style>



