export function getData () {
  return axios.get(`${host}/api/lahan`).then(resp => resp.data)
    .then(items => 
      items.map(it => {
        it.avatar = 'https://cdn.vuetifyjs.com/images/lists/1.jpg'
        return it
      }))
}

export function comp () {
  return axios.post(`${host}/comp`).then(resp => resp.data)
}

export function getMedias (id) {
  let url = `${host}/lahan/${id}/media`
  return axios.get(url)
    .then(x => x.data)
}

export function getAvatar (id) {
  let url = `${host}/lahan/${id}/avatar`
  return axios.get(url)
    .then(x => x.data)
}


