<template>
  <v-container id="loading-cont" fluid justify-center align-center fill-height style="min-height: 70vh;">
    <v-progress-circular size=256 color="amber" width="24" indeterminate></v-progress-circular>
  </v-container>
</template>

<script>
export default {
  data () {
    return {
      colors: ['teal', 'amber', 'yellow', 'dodgerblue']
    }
  },

  created () {
    setInterval(() => {

    })
  }
}
</script>

<style>
#loading-cont {
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  height: 100vh;
  background-color: rgba(0,0,0,0.5);
}
</style>



